import React from 'react';
import profile from './Profile.module.css';
import ProfileInfo from "./ProfileInfo/ProfileInfo";
import MyPostsContainer from "./MyPosts/MyPostsContainer";
import {setIsEditProfile} from "../../redux/profileReducer";

const Profile = (props) => {

    return (
       <div className={profile.content}>
            <ProfileInfo profile={props.profile} userId={props.userId} status={props.status}
                         updateStatus={props.updateStatus} isFetching={props.isFetching}
                         isStatusFetching={props.isStatusFetching}
                         isOwner={props.isOwner} uploadAvatar={props.uploadAvatar}
                         uploadAvaMsg={props.uploadAvaMsg} saveProfile={props.saveProfile}
                         isEditMode={props.isEditMode} setIsEditProfile={props.setIsEditProfile}
            />
           {!props.isEditMode &&
               <MyPostsContainer/>
           }
       </div>
    );
}

export default Profile;