import React from 'react';
import p from './Post.module.css';
import {NavLink} from "react-router-dom";

const Post = (props) => {
    const imgSrc = (props.profileSmallImg);
    return (
        <div className={p.item}>
            <div className={p.logodiv}>
                <img className={p.logo}
                     src={imgSrc}
                     alt=""/>
            </div>

            <div className={p.textCont}>
                <div className={p.txt}>
                    {props.msg}
                </div>

                <div>
                    <span>
                        <img className={p.likeImg} onClick={props.addLike} src="https://pngimg.com/uploads/like/like_PNG64.png" alt="small_user_img"/>
                        {(props.likeCount > 0) ? props.likeCount : null}
                    </span>
                </div>
            </div>



        </div>
    );
}

export default Post;