import React from "react";
import p from "./ProfileInfo.module.css";
import ProfileStatusFunctional from "./ProfileStatusFunctional";
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, minLengthCreator, required} from "../../../utils/validators/FormValidator";
import {Input, Textarea} from "../../common/FormControls/FormControls";
import style from "../../common/FormControls/FormControls.module.css";
import s from "../../Login/Login.module.css";

const Contacts = ({title, value}) => {
    return <div>{title}: {value} </div>
}

const maxLengthValidate30 = maxLengthCreator(30);
const maxLengthValidate150 = maxLengthCreator(150);

const minLengthCreator4 = minLengthCreator(4);

const EditProfileForm = ({profile, error, ...props}) => {


    return (
        <div className={p.userEditProfile}>
            <form onSubmit={props.handleSubmit}>
                {error && <div className={style.errorText}>{error}</div>}

                <div className={p.userName}>
                    {/*    Full Name:*/}
                    <Field validate={[required, maxLengthValidate30, minLengthCreator4]} component={Input}
                              type='text' name={'fullName'}/>
                </div>

                <div className={p.editLFJ + " " + p.userEditSkill}>
                    <label for="lookforjob">Looking for a job: </label>
                    <Field component={Input} id={"lookforjob"} type='checkbox' name={'lookingForAJob'}/>
                </div>
                <div className={p.userEditSkill}>
                    Skills:
                    <Field validate={[required, maxLengthValidate150, minLengthCreator4]} component={Textarea}
                           type='text' name={'lookingForAJobDescription'}/>
                </div>
                <div className={p.userEditSkill}>
                    About me:
                    <Field validate={[required, maxLengthValidate150, minLengthCreator4]} component={Textarea}
                           type='text' name={'aboutMe'}/>
                </div>

                <div className={'p.userContacts'}>
                    <div className={p.userContactsTitle}>My Contacts:</div>

                    {Object.keys(profile.contacts).map(k => {
                        return (<div className={p.editContactItem}>
                            <b style={{minWidth: '150px'}}>{k}: </b>
                            <Field key={k} placeholder={k} name={"contacts." + k} component={Input} type={'text'}/>
                        </div>)
                    })
                    }
                </div>
                    <div className={p.buttonContainer}>
                        <button className={p.button} >Save</button>
                        <button  className={p.button}  type={'reset'} onClick={() => {
                            props.setIsEditProfile(false)
                        }}>Cancel
                        </button>
                    </div>
            </form>
        </div>
    )
}


const EditProfileReduxForm = reduxForm({form: 'editProfile'})(EditProfileForm);

export default EditProfileReduxForm;