import React from "react";
import {connect} from "react-redux";
import Profile from "./Profile";
import {
    uploadAvatar,
    GetProfileData,
    getStatus,
    setUserProfile,
    updateStatus,
    saveProfile, setIsEditProfile
} from '../../redux/profileReducer';
import {withRouter} from "react-router-dom";
import {withAuthRedirect} from "../hoc/withAuthRedirect";
import {compose} from "redux";

class ProfileContainer extends React.Component {

    RefreshProfile () {
        let userId = this.props.match.params.userId;
        // let id = (!userId) ? (this.props.authId) : this.props.history.push("/login");
        if (!userId) {
            userId = this.props.authId;
            if (!userId) {
                this.props.history.push("/login");
            }
        }
        this.props.GetProfileData(userId);
        this.props.getStatus(userId);
    }

    componentDidMount() {
        this.RefreshProfile();
    }


    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.match.params.userId != prevProps.match.params.userId) {
            this.RefreshProfile();
        }
    }

    render() {

        return (
            <Profile {...this.props}
                     isOwner={!this.props.match.params.userId}
                     profile={this.props.profile}
                     updateStatus={this.props.updateStatus}
                     uploadAvatar={this.props.uploadAvatar}
                     isFetching={this.props.isFetching}
                     isStatusFetching={this.props.isStatusFetching}
                     isEditMode={this.props.isEditMode}
            />
        )
    }
}

let MapStateToProps = (state) => {
    return (
        {
            profile: state.profilePage.profile,
            authId: state.auth.userId,
            status: state.profilePage.status,
            isStatusFetching: state.profilePage.isStatusFetching,
            isFetching: state.profilePage.isFetching,
            uploadAvaMsg: state.profilePage.uploadAvaMsg,
            isEditMode: state.profilePage.isEditMode,
        }
    )
}
/*
let ProfileWithAuthRedirect = withAuthRedirect(ProfileContainer)

let WithRouterProfileContainer = withRouter(ProfileWithAuthRedirect);

export default connect(MapStateToProps, {setUserProfile, GetProfileData})(WithRouterProfileContainer);*/

export default  compose(connect(MapStateToProps,
    {setUserProfile, GetProfileData, updateStatus, getStatus, uploadAvatar, saveProfile, setIsEditProfile}),
    withRouter, withAuthRedirect)(ProfileContainer);