import React from 'react';
import s from './MyPosts.module.css';
import p from './Post/Post.module.css';
import Post from "./Post/Post";
import {Field, reduxForm} from "redux-form";
import {Textarea} from "../../common/FormControls/FormControls";
import {maxLengthCreator, required} from "../../../utils/validators/FormValidator";
// import Preloader from "../../common/Preloader/Preloader";

import noUserImage from "../../../assets/images/noimages.png"
import post from "./Post/Post.module.css";

const MyPosts = React.memo(
    (props) => {
        const profileSmallImg = (props.profile && props.profile.photos['small']) ?  props.profile.photos['small'] : noUserImage;
        let posts = props.posts;
        let postElements = posts.sort((a,b)=>b.id-a.id);
        postElements = posts.map((post, key) =>
            <Post key={key} msg={post.post}
                  addLike={()=> {props.addLike(post.id)}}
                  likeCount={post.likesCount} profileSmallImg={profileSmallImg}
            />
        );

        let onAddPost = props.addPost;

        const maxLength350 = maxLengthCreator(250);

        const PostForm = (props) => {

            // add post by Enter
            const handleKeyPress = e => {
                if (e.keyCode == 13 && e.shiftKey == false) {
                    props.handleSubmit()
                }
            };


            return (

                <div className={p.item}>
                    <div className={post.logodiv}>
                        <img className={post.logo}
                             src={profileSmallImg}
                             alt="userLogoImg"/>
                    </div>

                    <div >
                        <form className={p.formCont} action="" onSubmit={props.handleSubmit}>
                            <div className={p.textarea}>
                                {/*<Field component='textarea' placeholder='type something' name='post'/>*/}
                                <Field component={Textarea} placeholder='type something' name='post'
                                       validate={[maxLength350]}
                                       onKeyDown={handleKeyPress}
                                />
                            </div>
                            <div className={p.addPostButtonCont}>
                                <button className={p.addPostButton}>Add Post</button>
                            </div>
                        </form>
                    </div>
                </div>
            )
        };

        const AddPostForm = reduxForm({
            // a unique name for the form
            form: 'postForm'
        })(PostForm);

        const onSubmit = (textData) => {
            // console.log(textData);
            onAddPost(textData.post)
        }

        // console.log('render');
        return (
            <div className={s.container}>
                <div className={s.postsCont}>
                    <div>
                        <AddPostForm onSubmit={onSubmit}/>
                    </div>

                    <div className={s.posts}>
                        {postElements}
                    </div>
                </div>
            </div>
        )
    }
)

export default MyPosts;