import React from 'react';
import p from './ProfileInfo.module.css';
import userNoPhoto from "../../../assets/images/noimages.png";
import Preloader from "../../common/Preloader/Preloader";
// import ProfileStatus from "./ProfileStatus";
import editProfileButton from "../../../assets/images/editmode2.png"
import ProfileStatusFunctional from "./ProfileStatusFunctional";
import EditProfileReduxForm from "./EditProfileForm";
// import {saveProfile, setIsEditProfile} from "../../../redux/profileReducer";
import i from "../../Users/Users.module.css";


const toUpCase = (name) => {
    if (name && name.length>0) {
        let arr = name.split(" ");
        return arr.map(w => {
            return   w[0].toUpperCase() + w.slice(1);
        } ).join(" ");
    } else return false;
}

const ProfileInfo = ({saveProfile, ...props}) => {


    const Contacts = ({title, value}) => {
        return <div className={p.aboutMe}>
            <b>  {title} </b>: <a href={value}>{value}</a>
        </div>
    }


    if (!props.profile) {
        return <Preloader/>
    }


    const profile = props.profile;
    const bigImg = (props.profile.photos.large) ? props.profile.photos.large : userNoPhoto;

    const onDownloadAva = (e) => {
        // console.log(e.target.files[0]);
        e.target.files.length && props.uploadAvatar(e.target.files[0]);
    }

    const UploadAvaInformation = (props) => {
        if (props.uploadAvaMsg.error && props.uploadAvaMsg.error.length)
            return <div className={p.uploadMsg + " " + p.uploadError}>{props.uploadAvaMsg.error.join('\n')}</div>

        else if (props.uploadAvaMsg.success && props.uploadAvaMsg.success.length)
            return <div className={p.uploadMsg + " " + p.uploadSuccess}>{props.uploadAvaMsg.success.join('\n')}</div>

        else return <></>
    };


    const ProfileData = ({profile, isOwner, goSetEditMode}) => {
        return (
            <>
                <div className={p.test}>
                    <div className={p.userProfileData}>

                        <div className={p.userNameStatusCont}>
                            <div className={p.userNameCont}>
                                <div className={p.userName}>
                                    {toUpCase(profile.fullName)}
                                </div>

                                {/* edit profile button*/}
                                {isOwner &&
                                    <div className={p.editProfileButtonCont} onClick={goSetEditMode}>
                                        {/*Edit profile*/}
                                        <img className={p.editProfileButton} src={editProfileButton}
                                             alt="edit profile button"/>
                                    </div>
                                }
                            </div>
                            <div className={p.userStatus}>
                                {/*<ProfileStatus status={props.status} updateStatus={props.updateStatus} isFetching={props.isFetching}/>*/}
                                {isOwner ?
                                    <ProfileStatusFunctional status={props.status} updateStatus={props.updateStatus}
                                                             isOwner={props.isOwner}
                                                             isStatusFetching={props.isStatusFetching}/>
                                : props.status
                                }
                            </div>
                        </div>


                        {(profile.aboutMe) &&
                            <div className={p.aboutMe}>
                                <b>About me:</b> {profile.aboutMe}
                            </div>
                        }

                        <div className={p.aboutMe}>
                            {profile.lookingForAJob ? "I'm looking for a job" : ''}
                        </div>

                        {profile.lookingForAJob &&
                            <div className={p.aboutMe}>
                                <b>Skills:</b> {profile.lookingForAJobDescription}
                            </div>
                        }


                    </div>

                    <div className={p.userContacts}>
                        <div className={p.userContactsTitle}>My Contacts:</div>
                        {
                            Object.keys(profile.contacts).map(k => {
                                if (profile.contacts[k]) {
                                    return <Contacts key={k} title={k} value={profile.contacts[k]}/>
                                }
                            })}
                        {
                            (Object.values(profile.contacts).every(el => el === null || el === undefined)) &&
                            <div><i>Пользователь предпочел не указывать данные</i></div>

                        }

                    </div>
                </div>
            </>
        )
            ;
    }

    const onSubmit = (data) => {
        // console.log(data);
        saveProfile(data);
    }


    return (
        // <div className={p.content}>
        <div className={p.userProfile}>

            <div className={p.userImg}>

                {/* Аватарка*/}
                <div style={{display: 'flex', flexDirection: 'column', position: 'relative'}}>
                    {props.isFetching
                        ?
                        <>
                            <div className={i.loadCont}>
                                <div className={i.load}>
                                    <Preloader/>
                                </div>
                            </div>
                            <div className={p.profileImgContainer}>
                                <img style={{opacity: '0.4'}} src={bigImg} alt="avatar"
                                     className={p.profileImg}
                                />
                                {/* Кнопка загрузки изображения */}
                                {/*{props.isOwner &&
                                    <div className={p.inputButtonContainer}>
                                        <label className={p.inputButton}>
                                            <input type={'file'} onChange={onDownloadAva}/>
                                            <span className="input-file-btn">Изменить</span>
                                        </label>
                                    </div>}*/}
                            </div>
                        </>

                        :
                        <div className={p.profileImgContainer}>
                            <img src={bigImg} alt="avatar"
                                // className={(profile.lookingForAJob) ? p.lookingForAJob : p.notLookingForAJob}
                                 className={p.profileImg}
                            />
                            {/* Кнопка загрузки изображения */}
                            {props.isOwner &&
                                <div className={p.inputButtonContainer}>
                                    <label className={p.inputButton}>
                                        <input type={'file'} onChange={onDownloadAva}/>
                                        <span className="input-file-btn">Изменить</span>
                                    </label>
                                </div>}
                        </div>}
                        <UploadAvaInformation uploadAvaMsg={props.uploadAvaMsg}/>




                </div>


                {props.isEditMode ?
                    <EditProfileReduxForm {...props} onSubmit={onSubmit} initialValues={profile}/>
                    :
                    <ProfileData profile={profile} isOwner={props.isOwner}
                                 goSetEditMode={() => {
                                     props.setIsEditProfile(true)
                                 }}
                    />
                }

            </div>


        </div>
        // </div>
    );
}

export default ProfileInfo;